import { Axios } from 'axios'
import { ApiEntity } from '../../core'
import {
  User,
  UserRoutes,
  UsersClientModel,
  UsersSearchQuery,
} from '../user.model'

export class UsersClient extends ApiEntity implements UsersClientModel {
  constructor(axios: Axios) {
    super(axios)
  }

  /**
   * Search users by params
   * @param searchParams - the users search params
   * @returns the promise for search users
   */
  public search(searchParams?: UsersSearchQuery) {
    return this._search<User>(UserRoutes.v1Search, searchParams)
  }

  /**
   * List users by params
   * @param searchParams - the users search params
   * @returns the promise for search users
   */
  public list(searchParams?: UsersSearchQuery) {
    return this._list<User>(UserRoutes.v1Search, searchParams)
  }

  /**
   * Find a user by params
   * @param searchParams - the search params
   * @returns the promise for find a user
   */
  public findOne(searchParams?: UsersSearchQuery) {
    return this._findOne<User>(UserRoutes.v1Search, searchParams)
  }
}
